import Typography from 'components/Typography';
import Page from 'components/Page';
import React from 'react';

import {
  Card, CardBody, CardHeader, Col, Row
} from 'reactstrap';

const PublicPage = () => {
  return (
    <Page title="Public API">
      <Row>
        <Col>
          <Card>
            <CardHeader></CardHeader>
            <CardBody>
              <Typography>
                private NFT를 public NFT로 전환 시켜주기 위한 API를 제공합니다. <br/>
              </Typography>
            </CardBody>
          </Card>
        </Col>
      </Row>
     
      <Row>
        <Col>
          <Card>
            <CardHeader>PUBLIC NFT 전송</CardHeader>
            <CardBody>
              <Typography>
                개발 진행 중 <br/>
              </Typography>
            </CardBody>
          </Card>
        </Col>
      </Row>
      
      <Row>
        <Col>
          <Card>
            <CardHeader>PUBLIC NFT 발행</CardHeader>
            <CardBody>
              <Typography>
                private NFT를 public NFT로 전환 시켜주기 위한 API <br/>
              </Typography>
            </CardBody>
          </Card>
        </Col>
      </Row>

    </Page>
  );
};

export default PublicPage;
